import { htmlToText } from 'html-to-text';
import { AddressObject, Attachment, ParsedMail } from 'mailparser';

import { Office365Mail } from './office365-types';
import { MailAttachment, MailMessage } from './types';

export class Office365MailParser {
  async parsedToMailMessage(
    mail: Office365Mail,
    parsed: ParsedMail,
    receivedFallback: Date,
  ): Promise<MailMessage> {
    const date = parsed.date ?? receivedFallback;

    const headers: Array<{ key: string; header: string }> = [];
    const headerLines: Array<{ key: string; line: string }> = [];

    // mailparser exposes both structured and raw-ish headerLines
    parsed.headers.forEach((value, key) => {
      headers.push({
        key: String(key),
        header: Array.isArray(value) ? value.join(', ') : String(value),
      });
    });
    parsed.headerLines.forEach(({ key, line }) => {
      headerLines.push({ key, line });
    });

    const to = this.#extractAddrList(parsed.to);
    const cc = this.#extractAddrList(parsed.cc);
    const bcc = this.#extractAddrList(parsed.bcc);
    const from = this.#extractAddrList(parsed.from);
    const replyTo = this.#extractAddrList(parsed.replyTo);

    // Prefer HTML if present, but also supply text via htmlToText for consistency with Graph behavior.
    const html = typeof parsed.html === 'string' ? parsed.html : undefined;
    const text = parsed.text || (html ? htmlToText(html) : '');

    return {
      deltaTimestamp: receivedFallback.getTime(),
      deltaId: mail.id,
      originalMessageId: mail.internetMessageId,
      attachments: await this.#parseAttachments(parsed.attachments ?? []),
      headers,
      headerLines,
      subject: parsed.subject || undefined,
      references: parsed.references || undefined,
      date,
      to,
      from,
      cc,
      bcc,
      replyTo,
      text: (text || '').trim(),
      html,
      messageId: parsed.messageId || undefined,
      inReplyTo: parsed.inReplyTo || undefined,
    } as MailMessage;
  }

  formatMailReplyBody(orig: Office365Mail, body: string): string {
    // Format Outlook-style separator and metadata
    const origFrom = orig.from?.emailAddress?.address || '';
    const origTo =
      orig.toRecipients
        ?.map((r) => r.emailAddress?.address)
        .filter(Boolean)
        .join('; ') || '';
    const origSubject = orig.subject || '';
    const origDate = orig.sentDateTime
      ? new Date(orig.sentDateTime).toLocaleString()
      : '';
    const separator =
      `<br><hr style="border:none;border-top:solid #b1b1b1 1px;height:1px;margin:16px 0 8px 0;" /><br>` +
      `From: ${origFrom}<br>` +
      `Sent: ${origDate}<br>` +
      `To: ${origTo}<br>` +
      `Subject: ${origSubject}<br><br>`;
    const origBody = orig.body?.content || '';

    return `${body}${separator}${origBody}`;
  }

  #extractAddrList(
    field?: AddressObject | Array<AddressObject>,
  ): Array<string> | undefined {
    if (!field) {
      return undefined;
    }

    const pluck = (x: any): Array<string> | undefined => {
      if (!x || typeof x !== 'object') {
        return [];
      }

      // address object: { address?: string }
      if ('address' in x && typeof x.address === 'string' && x.address) {
        return [x.address];
      }
      // group object: { group?: AddressObject[] }
      if ('group' in x && Array.isArray((x as any).group)) {
        return (x as any).group.flatMap(pluck);
      }
      return [];
    };

    // mailparser v3 style: { value: AddressObject[] }
    if ('value' in field && Array.isArray(field.value)) {
      const out = field.value.flatMap(pluck).filter(Boolean);
      return out.length ? out : undefined;
    }

    // mailparser v2 style: AddressObject | AddressObject[]
    if (Array.isArray(field)) {
      const out = field.flatMap(pluck).filter(Boolean);
      return out.length ? out : undefined;
    }

    const out = pluck(field).filter(Boolean);
    return out.length ? out : undefined;
  }

  async #parseAttachments(
    attachments: Array<Attachment>,
  ): Promise<Array<MailAttachment>> {
    return (
      await Promise.all(
        attachments.map(async (att): Promise<MailAttachment | undefined> => {
          const ct = (att.contentType || '').toLowerCase();
          const fn = (att.filename || 'attachment').toLowerCase();
          const isJson = ct === 'application/json' || fn.endsWith('.json');

          if (!isJson) {
            return undefined;
          }

          const buf = Buffer.isBuffer(att.content)
            ? att.content
            : await new Promise<Buffer>((resolve, reject) => {
                const chunks: Array<Buffer> = [];
                (att.content as any)?.on?.('data', (c: Buffer) =>
                  chunks.push(c),
                );
                (att.content as any)?.once?.('end', () =>
                  resolve(
                    Buffer.concat(
                      chunks as unknown as ReadonlyArray<Uint8Array>,
                    ),
                  ),
                );
                (att.content as any)?.once?.('error', reject);
              });

          return {
            filename: att.filename || 'attachment.json',
            contentType: 'application/json',
            content: buf.toString('utf-8'),
          };
        }),
      )
    ).filter((att) => !!att);
  }
}
